<template>
	<view>
		<view v-if="audiolist.length">
			<zaudio themeColor="#6ebcff" ></zaudio>
			<u-textarea 
				class="u-page2" 
				v-model="comment" 
				placeholder="请输入评论内容" 
				:count="true" 
				confirmType="发送"
				@blur="blur">
			</u-textarea>
			<view class="u-page" v-if="comments.length || hotComments.length">
				<u-divider text="热评" :hairline="true"></u-divider>
				<!-- HotComment -->
				<view class="album" v-for="(item, index) in hotComments" :key="item.time">
					<view class="album__avatar">
						<u-image
								:src="item.user.avatarUrl"
								mode=""
								width=32
								height=32
						></u-image>
					</view>
					<view class="album__content">
						<u--text
								:text="item.user.nickname"
								type="primary"
								bold
								size="16"
						></u--text>
						<u--text
								margin="0 0 16rpx 0"
								:text="item.timeStr"
								size="12"
						></u--text>
						<u--text
								margin="0 0 16rpx 0"
								:text="item.content"
						></u--text>
						<u-album :urls="urls1" keyName="src2"></u-album>
						<u--text
							prefixIcon="heart"
							:text="item.likedCount"
							size=12
							margin="16rpx 0 32rpx 0"
							color="#bfbfbf" 
							iconStyle="color: #bfbfbf;"
							>
						</u--text>
					</view>
				</view>
				<u-divider :hairline="true"></u-divider>
				<!-- Comment -->
				<view class="album" v-for="(item, index) in comments" :key="item.time">
					<view class="album__avatar">
						<image
								:src="item.user.avatarUrl"
								mode=""
								style="width: 32px;height: 32px;"
						></image>
					</view>
					<view class="album__content">
						<u--text
								:text="item.user.nickname"
								type="primary"
								bold
								size="16"
						></u--text>
						<u--text
								margin="0 0 16rpx 0"
								:text="item.timeStr"
								size="12"
						></u--text>
						<u--text
								margin="0 0 16rpx 0"
								:text="item.content"
						></u--text>
						<u-album :urls="urls1" keyName="src2"></u-album>
						<u--text
							prefixIcon="heart"
							:text="item.likedCount"
							size=12
							margin="0 0 32rpx 0"
							color="#bfbfbf" 
							iconStyle="color: #bfbfbf;"
							>
						</u--text>
					</view>
				</view>
				<u-loadmore
					:line="true"
					status="loadmore"
					@loadmore="loadmore"
					v-if="!base"
				></u-loadmore>
				<u-loadmore
					:line="true"
					status="nomore"
					v-else
				></u-loadmore>
			</view>
		</view>
		<u-empty
			mode="data"
			icon="http://cdn.uviewui.com/uview/empty/data.png"
			v-else
		>
		</u-empty>
	</view>
</template>

<script>
	import { getSongComment, sendDelComment } from '@/http/http.api.js';
	import zaudio from '@/components/uniapp-zaudio/zaudio';
	export default {
		components: {zaudio},
		data() {
			return {
				audiolist: this.$zaudio.audiolist, //当前音频列表
				
				urls1: [],
				
				offset: 1,
				limit: 10,
				
				songId: 0,
				
				comments: [],
				hotComments: [],
				
				// comment加载到底了
				base: false,
				
				// 待发送评论
				comment: '',
				t: 1,
				type: 0,
				
			}
		},
		methods: {
			
			getComments(){
				this.$zaudio.audiolist.forEach((item, index) => {
					if(this.$zaudio.playinfo.src === item.src){
						this.songId = this.$zaudio.audiolist[index].id;
						console.log(this.songId);
							getSongComment({ params: { id: this.songId, limit: this.limit, offset: (this.offset - 1) * this.limit } }).then(res => {
								this.comments = res.data.comments;
								this.hotComments = res.data.hotComments;
							}).catch(e => {
								console.log(e);
							});
					};
				});
			},
			loadmore(){
				this.loading = true;
				this.offset = this.offset + 1;
				console.log(this.offset);
				getSongComment({ params: { id: this.songId, limit: this.limit, offset: (this.offset - 1) * this.limit } }).then(res => {
					if(res.data.comments.length !== 0){
						this.comments = this.comments.concat(res.data.comments);
					}else{
						this.base = true;
						this.loading = false;
						this.$u.toast('到底了');
					}
				}).catch(e => {
					console.log(e);
				});
			},
			blur(e){
				console.log(e.target.value);
				if(Object.keys(this.vuex_loginMsg).length !== 0){
					if(this.comment !== ''){
						sendDelComment({ params: { t: this.t, type: this.type, id: this.songId, content: this.comment } }).then(res => {
							this.comment = '';
							this.$u.toast('评论发送成功');
						}).catch(e => {
							console.log(e);
						});
					}else{
						this.$u.toast('请填写内容后再发送');
					}
				}else{
					this.$u.toast('请先登录');
				}
			}
		},
		onLoad(){
			this.base = false;
			this.offset = 1;
			this.getComments();
		},
		onShow(){
			//实时渲染当前的播放状态
			this.$zaudio.syncRender();
			this.$zaudio.on('canPlay', 'event-a', data => {
				this.loading = true;
				this.base = false;
				this.offset = 1;
				this.getComments();
			});
		},
		onUnload() {
			this.$zaudio.off('canPlay', 'event-a');
		}
	}
</script>

<style lang="scss" scoped>
	.u-page{
		padding: 40rpx;
	}
	
	.u-page2{
		margin: 40rpx;
	}
	
	.album {
		@include flex;
		align-items: flex-start;
	
		&__avatar {
			 background-color: $u-bg-color;
			 padding: 10rpx;
			 border-radius: 6rpx;
		 }
	
		&__content {
			 margin-left: 20rpx;
			 flex: 1;
		 }
	}
</style>
